import popupLayer from '../../components/popup-layer/popup-layer.vue';
import {getAddress} from '../../common/fetch.js';
export default {
    components: {
        popupLayer
    },
    data(){
        return {
            show: false, // 遮罩层
            wl_show: false, // 物流选择
            checked: true,
            checked1: true,
            checked2: true,
            checked3: true,
            wl_list: [
                {name:'顺丰',price:'免邮',index:0},
                {name:'中通',price:'免邮',index:1},
                {name:'圆通',price:'￥20',index:2}
            ],
			Users_ID:'wkbq6nc2kc',
			User_ID:3,
			addressinfo: {}, // 收货地址信息
        }
    },
	filters: {
		formatphone: function(value) {
			console.log(value)
			if(value) {
				var len= value.length;
				var xx= value.substring(3,len-4);
				var values = value.replace(xx,"****");
				return values;				
			}
		}
	},
	created() {
		this.getAddress();
	},
    methods: {
        // 选择运费
        changeShip(){
            this.$refs.popupRef.show();
        },
		closeMethod(){
			this.$refs.popupRef.close();
		},
		getAddress(){
			getAddress({Users_ID: this.Users_ID,User_ID: this.User_ID,}).then(res=>{
				console.log(res)
				if(res.errorCode == 0) {
					for(let i in res.data){
						for(let j in res.data[i]){
							if(j=='Address_Is_Default'){
								res.data[i][j] == 1;
								this.addressinfo = res.data[i]
							}
						}
					}
					console.log(this.addressinfo)
				}
			}).catch(e => console.log(e))
		}
    }
}
